
import { formatDate } from "@/common/js/util";
import { callPuExctQryToOICS } from "@/api/api";
import OFooter from "@/components/common/OFooter";
import OPosition from "@/components/common/OPosition";
import OUploader from "@/components/common/OUploader";
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
directives: {
TransferDom
},
components: {
Group,
XTextarea,
XInput,
XButton,
ViewBox,
OFooter,
OUploader,
Flexbox,
FlexboxItem,
Cell,
XTable,
Checklist,
AlertModule,
Loading
},
data() {
return {
feedbackSuperviseItem: {}, //存入localStorage的一条录入核查反馈意见任务数据
feedbackResultList: [
{ key: "1", value: "同意" },
{ key: "2", value: "不同意" }
], //反馈意见选择项
feedbackResult: [], //反馈意见
feedbackComment: "", //反馈意见说明
illegalTypeList: [
{ key: "0", value: "主要" },
{ key: "1", value: "次要" }
], //相关人员类型选择项
relevantPersonnelList: [], //相关人员初始化
propUploaderTitle: "上传图片", //图片控件标题
propLableStyle: {
width: "6.5em",
textAlign: "justify",
marginRight: "2em"
}, //图片控件标题样式
propMaxCount: 50, //上传图片最大数量
uploadList: [], ////初始化，选择的图片列表，待上传至服务器
propSuccessImgList: [], //初始化，已经上传成功的图片列表，接口返回数据
showContent: false, //相关人员显示与隐藏
disabledButton: false,
number: function(value) {
return {
valid: !value.replace(/[`\d]/g, ""),
msg: "只能输入数字"
};
},
reqUploadList: [], //需要执行批次上传的图片集合，现在每次上传2张
submintState: "", //"01-保存，02-提交"
pageIndex: 0,
pageSize: 2,
loadingShow: false,
loadingText: "请求中",
imgMax: 0, //待上传的图片总数
imgCount: 0, //上传成功的图片数量
isDefault: false 
};
},
watch: {},
methods: {

replaceValue(value) {
this.feedbackComment = value
.replace(/ /g, "")
.replace(/\u2006/g, "");
},


getFeedbackSuperviseDetail() {
let params = {
params: {
RuleName: "GetFeedbackSuperviseDetail",
Parm: {
SuperviseNo: this.feedbackSuperviseItem.SuperviseNo
}
}
};
let callBackSuccess = data => {
if (data.ReturnCd == "00") {
let dataOne = data.RspMsg;
if (dataOne.FeedbackResult && dataOne.FeedbackResult != "")
this.feedbackResult.push(dataOne.FeedbackResult);
this.feedbackComment = dataOne.FeedbackComment;
this.relevantPersonnelList =
dataOne.result.ListItems.ListItem;
let IllegalTypeList = [];
for (
let i = 0;
i < this.relevantPersonnelList.length;
i++
) {
IllegalTypeList.push(
this.relevantPersonnelList[i].IllegalType
);
this.relevantPersonnelList[
i
].IllegalTypeList = IllegalTypeList;
IllegalTypeList = [];
}
} else {
this.loadingShow = false;
this.showModule("获取数据失败", data.RspMsg.msg);
}
};
let callBackError = data => {
this.loadingShow = false;
this.showToast("获取数据失败:" + data, "20.0em", "warn");
};
callPuExctQryToOICS(params, callBackSuccess, callBackError);
}, //查询图片
getImageItems() {
if (this.loadingShow == false) {
return;
}
this.pageIndex++;
let params = {
params: {
RuleName: "GetImageItems",
Parm: {
CaseNo: this.feedbackSuperviseItem.SuperviseNo,
PageSize: this.pageSize,
PageIndex: this.pageIndex
}
}
};
let callBackSuccess = data => {
if (data.ReturnCd == "00") {
this.propSuccessImgList = this.propSuccessImgList.concat(
data.RspMsg.result.ListItems.ListItem || []
);
setTimeout(() => {
this.imgMax = data.RspMsg.total;
this.imgCount =
this.imgCount +
data.RspMsg.result.ListItems.ListItem.length;
if (
!data.RspMsg.result.ListItems.ListItem ||
this.imgCount == data.RspMsg.total ||
data.RspMsg.result.ListItems.ListItem.length !=
this.pageSize
) {
this.loadingShow = false;
} else {
this.getImageItems();
}
}, 10);
} else {
this.loadingShow = false;
this.showModule("获取图片失败", data.RspMsg.msg);
}
};
let callBackError = data => {
this.loadingShow = false;
this.showToast("获取图片失败", "20.0em", "warn");
};
callPuExctQryToOICS(params, callBackSuccess, callBackError);
}, //弹出信息提示窗
showModule(title, content) {
AlertModule.show({
title: title,
content: content,
onShow() {

},
onHide() {

}
});
},
showToast(text, width, type) {
this.$vux.toast.show({
text: text,
type: type, //success, warn, cancel, text
width: width
});
},
change(val, label) {

}, //添加相关人员
addConcernedPeople() {

var addArray = {
IllegalTypeList: [],
PersonId: ""
};
this.relevantPersonnelList.push(addArray);
}, //移除相关人员
removeConcernedPeople(index) {

this.relevantPersonnelList.splice(index, 1);
}, //获取上传的图片信息
getUploadList(uploadList) {
this.uploadList = uploadList;


}, //录入核查反馈意见-保存、提交
handleFeedbackSupervise(submintState) {

let personItems = []; 
this.submintState = submintState;
let errMsg =
submintState == "01"
? "录入核查反馈意见保存失败"
: "录入核查反馈意见提交失败";
if (this.feedbackResult.length <= 0) {
this.showToast("请选择反馈意见", "10.0em", "cancel");
return;
}

if (this.feedbackResult[0] == "1") {
if (this.relevantPersonnelList.length <= 0) {
this.showToast("相关人员不能为空", "10.0em", "cancel");
return;
} else {
let count = 0;
for (
let i = 0;
i < this.relevantPersonnelList.length;
i++
) {
let IllegalTypeList = this.relevantPersonnelList[i]
.IllegalTypeList;
let PersonId = this.relevantPersonnelList[i].PersonId;
if (IllegalTypeList.length <= 0) {
this.showToast(
"相关人员第" + ++i + "行类型不能为空",
"14.0em",
"cancel"
);
return;
}
if (PersonId == "") {
this.showToast(
"相关人员第" + ++i + "行工号不能为空",
"14.0em",
"cancel"
);
return;
}
if (PersonId.replace(/[`\d]/g, "")) {
this.showToast(
"相关人员第" + ++i + "行工号只能输入数字",
"16.0em",
"cancel"
);
return;
}
if (PersonId.length < 8) {
this.showToast(
"相关人员第" + ++i + "行工号只能输入8位数字",
"17.0em",
"cancel"
);
return;
}
if (IllegalTypeList[0] == "0") {
++count;
}
personItems.push({
PersonId: PersonId,
IllegalType: IllegalTypeList[0]
});
}
if (count <= 0) {
this.showToast(
"相关人员至少要有一个主要责任人",
"16.0em",
"cancel"
);
return;
}
}
} else {
if (this.relevantPersonnelList.length > 0) {
for (
let i = 0;
i < this.relevantPersonnelList.length;
i++
) {
let IllegalTypeList = this.relevantPersonnelList[i]
.IllegalTypeList;
let PersonId = this.relevantPersonnelList[i].PersonId;
if (IllegalTypeList.length > 0 && PersonId != "") {
if (PersonId.replace(/[`\d]/g, "")) {
this.showToast(
"相关人员第" + ++i + "行工号只能输入数字",
"16.0em",
"cancel"
);
return;
}
if (PersonId.length < 8) {
this.showToast(
"相关人员第" +
++i +
"行工号只能输入8位数字",
"17.0em",
"cancel"
);
return;
}
personItems.push({
PersonId: PersonId,
IllegalType: IllegalTypeList[0]
});
}
}
}
}
if (this.feedbackComment == "") {
this.showToast("请输入反馈意见说明", "11.0em", "cancel");
return;
}


this.loadingText = "请求中";
this.loadingShow = true;
this.isDefault = false;
this.disabledButton = true;
const _this = this;
let params = {
params: {
RuleName: "HandleFeedbackSupervise",
Parm: {
TimePoint: formatDate(new Date(), "yyyyMMdd"),
SuperviseNo: this.feedbackSuperviseItem.SuperviseNo,
FeedbackResult: this.feedbackResult[0],
FeedbackComment: this.feedbackComment,
PersonItems: {
ListItem: personItems
},
HandleState: submintState
}
}
};
let callBackSuccess = data => {

if (
data.ReturnCd == "00" &&
data.RspMsg.result[0].DataFlag == "01"
) {









if (this.$refs.uploader.addImgCount > 0) {


for (let i = 0; i < this.uploadList.length; i++) {
let item = this.uploadList[i];
if (item.ImageId < 0) {
this.reqUploadList.push({
ImageId: item.ImageId,
FileName: item.FileName,
FileContent: item.src.slice(23)
});
}
}
this.imgMax = this.reqUploadList.length;
this.imgCount = 0;

this.handleImageItems();
} else {

this.loadingShow = false;
this.$vux.toast.show({
text:
this.submintState == "01"
? "录入核查反馈意见保存成功"
: "录入核查反馈意见提交成功",
width: "15em",
onHide() {
_this.$router.go(-1);
}
});
}

if (this.$refs.uploader.deleteImgList.length > 0) {
this.deleteHandleImageItems();
}
} else {
this.loadingShow = false;
this.showModule(errMsg, data.RspMsg.msg);
this.disabledButton = false;
}
};
let callBackError = data => {
this.loadingShow = false;
this.showModule(errMsg, data.RspMsg.msg);
this.disabledButton = false;
};
callPuExctQryToOICS(params, callBackSuccess, callBackError);
}, //返回上一页
onBack() {
this.localStorageRemoveItem();
}, //返回主页
onHomeMenu() {
this.localStorageRemoveItem();
}, //移除localStorage数据
localStorageRemoveItem() {
localStorage.removeItem("FeedbackSuperviseItem");
}, //上传图片
handleImageItems() {


const _this = this;
let imgList = this.reqUploadList.splice(0, 2);
this.loadingText = "上传图片中";
this.isDefault = true;
this.imgCount = this.imgCount + imgList.length;
let params = {
params: {
RuleName: "HandleImageItems",
Parm: {
CaseNo: this.feedbackSuperviseItem.SuperviseNo,
HandleState: "02",
ListItems: {
ListItem: imgList
}
}
}
};
let callBackSuccess = data => {

if (
data.ReturnCd == "00" &&
data.RspMsg.result[0].DataFlag == "01"
) {
if (this.reqUploadList.length > 0) {
this.handleImageItems();
} else {

this.loadingShow = false;
this.$vux.toast.show({
text:
this.submintState == "01"
? "录入核查反馈意见保存成功"
: "录入核查反馈意见提交成功",
width: "15em",
onHide() {
_this.$router.go(-1);
}
});
}
} else {
this.loadingShow = false;
this.showModule("上传图片失败", data.RspMsg.msg);

}
};
let callBackError = data => {
this.loadingShow = false;
this.showToast("上传图片失败", "20.0em", "warn");

};
callPuExctQryToOICS(params, callBackSuccess, callBackError);
}, //删除图片
deleteHandleImageItems() {
let params = {
params: {
RuleName: "HandleImageItems",
Parm: {
CaseNo: this.feedbackSuperviseItem.SuperviseNo,
HandleState: "04",
ListItems: {
ListItem: this.$refs.uploader.deleteImgList
}
}
}
};
let callBackSuccess = data => {

let item = null;
if (
data.ReturnCd == "00" &&
data.RspMsg.result[0].DataFlag == "01"
) {
} else {
_this.showModule("删除图片失败", data.RspMsg.msg);
}
};
let callBackError = data => {
this.loadingShow = false;
this.showToast("删除图片失败", "20.0em", "warn");

};
callPuExctQryToOICS(params, callBackSuccess, callBackError);
},
getImgCount() {
return this.imgCount + "/" + this.imgMax;
}
},
mounted() {


},
beforeRouteEnter(to, from, next) {
next(vm => {
vm.loadingText = "加载中";
vm.loadingShow = true;
vm.isDefault = false;
vm.uploadList = [];
vm.propSuccessImgList = [];
vm.feedbackResult = []; 
vm.feedbackComment = ""; 
vm.relevantPersonnelList = []; 
vm.showContent = false; 
vm.disabledButton = false;
vm.pageIndex = 0;
vm.$refs.uploader.addImgCount = 0;
vm.reqUploadList = []; 
vm.submintState = "";
vm.$refs.uploader.deleteImgList = []; 
vm.imgMax = 0; 
vm.imgCount = 0; 
vm.feedbackSuperviseItem = JSON.parse(
localStorage.getItem("FeedbackSuperviseItem")
);

if (vm.feedbackSuperviseItem.SuperviseStatus == "1") {
vm.loadingText = "图片加载中";
vm.isDefault = true;
vm.getFeedbackSuperviseDetail();
vm.getImageItems();
} else {
vm.isDefault = false;
vm.loadingShow = false;
}
});
}, //控制浏览大图不返回
beforeRouteLeave(to, from, next) {
let isClickImg = window.sessionStorage.getItem("isClickImg");
if (isClickImg == "0") {
this.loadingShow = false;
next();
} else {
this.$refs.uploader.$refs.previewer.close();
next(false);
}
}
};



